import {
  searchAlarm,
  devicetype,
  devicename,
  deviceattr,
} from '../../utils/service';
import {
  Loading
} from 'element-ui';
import {
  DxSelection,
  DxDataGrid,
  DxColumn,
  DxFilterRow,
  DxPager,
  DxFilterPanel,
  DxPaging,
  DxHeaderFilter,
  DxScrolling
} from "devextreme-vue/data-grid";
import zhMessages from "devextreme/localization/messages/zh.json";
import {
  locale,
  loadMessages
} from "devextreme/localization";
export default {
  created() {
    loadMessages(zhMessages);
    locale(navigator.language);
  },
  components: {

    DxSelection,
    DxDataGrid,
    DxColumn,
    DxFilterRow,
    DxPager,
    DxFilterPanel,
    DxPaging,
    DxHeaderFilter,
    DxScrolling

  },
  data() {
    return {
      pickerOptions: {
        shortcuts: [{
          text: '最近一周',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 7);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近一个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 30);
            picker.$emit('pick', [start, end]);
          }
        }, {
          text: '最近三个月',
          onClick(picker) {
            const end = new Date();
            const start = new Date();
            start.setTime(start.getTime() - 3600 * 1000 * 24 * 90);
            picker.$emit('pick', [start, end]);
          }
        }]
      },
      devicename: '', // 设备名称
      devicetype: '', // 设备类型
      deviceattr: '', // 设备属性
      timedata: '',
      sdevicename: '', // 确认设备名称
      sdevicetype: '', // 确认设备类型
      sdeviceattr: '', // 确认设备属性
      datalist: '',
      pageSizes: [10, 20, 100],
      displayMode: "full",
      showPageSizeSelector: true,
      showInfo: true,
      showNavButtons: true,
      clientHeight: '',
      tableList: [{
        title: '报警日期',
        value: 'alarm_date',
      }, {
        title: '设备类型',
        value: 'typename',
      }, {
        title: '设备名称',
        value: 'device_name',
      }, {
        title: '安装位置',
        value: 'position',
      }, {
        title: '报警内容',
        value: 'content',
      }, ],
      total: 0,
      pagesize: 20,
      clientHeight: '',
    };
  },
  created() {

  },
  mounted() {
    this.fgetdata()
    this.getdevicetype()
    this.clientHeight = document.documentElement.clientHeight - 250
  },
  methods: {
    // prevclick(e) {
    //   console.log('e :>> ', e);
    // },
    handleCurrentChange(e) {
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata.length; index++) {
        startdate = this.timedata[0];
        enddate = this.timedata[1];
      }
      var formData = {
        startdate: startdate,
        enddate: enddate,
        deviceid: this.sdevicename,
        typecode: this.sdevicetype,
        pageindex: e,
        pagesize: this.pagesize,
      }
      searchAlarm(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    fgetdata() {
      searchAlarm({}, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    topsure() {
      var startdate = ''
      var enddate = ''
      for (let index = 0; index < this.timedata.length; index++) {
        startdate = this.timedata[0];
        enddate = this.timedata[1];
      }
      var formData = {
        startdate: startdate,
        enddate: enddate,
        deviceid: this.sdevicename,
        typecode: this.sdevicetype,
      }
      searchAlarm(formData, res => {
        this.total = res.count
        this.datalist = res.data
        this.datalist.map((x, index) => {
          x.key = index + 1;
        });
      });
    },
    // 获取设备类型信息
    getdevicetype() {
      devicetype({}, res => {
        this.devicetype = res.data

      });
    },
    // 根据设备类型获取设备名称
    changesdevicetype(e) {
      this.sdevicetype = e
      this.sdevicename = ''
      var data = {
        devicetype: this.sdevicetype,
      }
      devicename(data, res => {
        this.devicename = res.data
      });
    },
    changesdevicename(e) {
      this.sdevicename = e
    },
    handleSizeChange(e) {
      this.pagesize = e
    },
  }
};



