<template>
  <div class="alarmInformation bg">
    <div class="title">
      <span></span>
      <div class="title_name">报警信息查询</div>
    </div>
    <div class="choice" style="">
      <div class="data">
        <span>报警时间：</span>
        <el-date-picker v-model="timedata" type="datetimerange" align="right" start-placeholder="开始日期"
          value-format="yyyy-MM-dd HH:mm:ss" end-placeholder="结束日期" :default-time="['00:00:00', '23:59:59']"
          :picker-options="pickerOptions">
        </el-date-picker>
      </div>
      <div>
        <span>设备类型：</span>
        <el-select v-model="sdevicetype" clearable placeholder="请选择" @change="changesdevicetype">
          <el-option v-for="(item, i) in devicetype" :key="i" :label="item.name" :value="item.devicetype">
          </el-option>
        </el-select>
      </div>
      <div class="inputname">
        <span class="sbname">设备名称：</span>
        <el-select v-model="sdevicename" clearable placeholder="请选择">
          <el-option v-for="(item, i) in devicename" :key="i" :label="item.device_name" :value="item.device_id"
            @change="changesdevicename">
          </el-option>
        </el-select>
      </div>
      <div>
        <el-button type="success" plain size="medium" class="button" @click="topsure">确认</el-button>
      </div>
    </div>
    <div class="alarmInformation_main">
      <!-- <div v-show="datalist.length == 0" class="nodata">加载中... </div> -->
      <div class="table" v-show="datalist.length != 0">
        <DxDataGrid :data-source="datalist" :allow-column-resizing="true" :show-column-lines="true"
          :auto-expand-all="false" :show-row-lines="true" :column-auto-width="true" :focused-row-enabled="true"
          :auto-navigate-to-focused-row="true" width="98%" key-expr="key" class="main_table" style="width:98%"
          :height="clientHeight">
          <DxColumn :width="250" data-field="alarm_date" alignment="center" caption="报警日期" />
          <DxColumn :width="260" data-field="typename" alignment="center" caption="设备类型" />
          <DxColumn :width="260" data-field="device_name" alignment="center" caption="设备名称" />
          <DxColumn :width="260" data-field="position" alignment="center" caption="安装位置" />
          <DxColumn :width="260" data-field="content" alignment="center" caption="报警内容" />
          <!-- <DxPaging :page-size="pagesize" /> -->
          <!-- <DxPager :visible="true" :allowed-page-sizes="pageSizes" :display-mode="displayMode"
                    :show-page-size-selector="showPageSizeSelector" :show-info="showInfo"
                    :show-navigation-buttons="showNavButtons" /> -->
          <DxScrolling use-native="false" />
        </DxDataGrid>
        <!-- <table class="list-table">
                    <thead>
                        <tr class="table-title">
                            <th class="tac" v-for="(item0, i0) in tableList" :key="i0">{{ item0.title }} </th>
                        </tr>
                    </thead>
                    <tbody>
                        <tr class="table-content" v-for="(item, i) in  datalist" :key="i">
                            <td v-for="(item1, i1) in tableList" :key="i1">{{ item[item1.value] }}</td>
                        </tr>
                    </tbody>
                </table> -->
      </div>
      <el-pagination :page-size="pagesize" :pager-count="5" :page-sizes="[10, 20, 30, 40]"
        @size-change="handleSizeChange" layout="total, sizes, prev, pager, next, jumper" :total="total"
        @current-change="handleCurrentChange" v-show="datalist.length != 0" style="margin-left: 25px;
padding-left: 20px;">
      </el-pagination>
    </div>
  </div>
</template>

<script>
import alarmInformation from "./alarmInformation";
export default alarmInformation;
</script>
<style scoped>
@import url("alarmInformation.css");
@import url("../../assets/css/common.css");
</style>



